import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../../../../helpers';
import { ScriptLoaderService } from '../../../../../../_services/script-loader.service';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../../animations/round-anticlock.animation';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../../animations/fly-in-out.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {ListComponent, SafeHtmlPipe} from '../../../../list/list.component'
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import { FormControl, Validators, FormGroup, ValidationErrors } from '@angular/forms';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge'; 
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/filter' ;
import { ToastComponent } from '../../../../../layouts/toast-notification/toast.component';


const PHONE_REGEX=/^[91]{2}-[80]{2}-[0-9]{8}$/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./finance-company-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class FinanceCompanyAddComponent    {
    @HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;
	
	 financecompanyForm: FormGroup;
	 fincompId = 0; 
	 status;
	 financecompany= new FinanceCompanyData();  
	 bootstrapFormValues = [];
	 msg = [];
 


 constructor(private http: HttpClient, private router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.fincompId).subscribe(params=>{
			if(params.fincompId != undefined){
				this.fincompId=params.fincompId; 
				}
		}); 
	
	if(this.fincompId == 0){
		this.status="Add";
	}else{
		this.status="Update";
		this.populateData();
	}
	}
	
     ngOnInit() {
		this.financecompanyForm = new FormGroup({			
			'fincompId' : new FormControl(''),
			'fincompName' : new FormControl('', [ Validators.required]),
			'fincompActive' : new FormControl(''),
			'fincompEntryId' : new FormControl(''),
			'fincompEntryDate' : new FormControl(''),
			'fincompModifiedId' : new FormControl(''),
			'fincompModifiedDate' : new FormControl(''),
		});
  }
	  
	    get fincompName(){ return this.financecompanyForm.get('fincompName')};
	    get fincompActive(){ return this.financecompanyForm.get('fincompActive')};
	    get fincompEntryId(){ return this.financecompanyForm.get('fincompEntryId')};
	    get fincompEntryDate(){ return this.financecompanyForm.get('fincompEntryDate')};
	    get fincompModifiedId(){ return this.financecompanyForm.get('fincompModifiedId')};
	    get fincompModifiedDate(){ return this.financecompanyForm.get('fincompModifiedDate')};

	  
	  populateData(){
		const headers = new HttpHeaders({'Content-Type':'application/json'});
		
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/head-data',
									JSON.stringify({ fincompId :this.fincompId,
												   	requestType : "update" }),
						{ headers, withCredentials: true }).subscribe(
			 										data => {
			 													this.financecompany = data['populateData'][0];
															},
													(err) => {
																console.log("ERROR: "+err);
															 });			 
		}
	   
	 
	  
	  ngAfterViewInit(){
		    $.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){
		   });
	  }
 
 	 toggleAnimation(){
			this.router.navigate(['portal/finance-company-list']);
		 }
		   

	onSubmit() {
			this.bootstrapFormValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFormValues.length;i++){
				this.financecompany[this.bootstrapFormValues[i].name]=this.bootstrapFormValues[i].value;
			}
		
		 if(!this.financecompanyForm.invalid){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
		if(this.fincompId == 0){ 
			this.financecompany['requestType'] = 'add';
		}else{
			this.financecompany['requestType'] = 'update';
			this.financecompany['fincompId'] = this.fincompId+'';
		}
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/head',
									JSON.stringify(this.financecompany),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}else{
														this.msg[0]='Error';
			 											this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.financecompanyForm.reset();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
														});
			
			}
	  }


  onDelete(){
		 this.financecompany['requestType'] = 'delete';
		 this.financecompany['fincompId'] = this.fincompId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/financecompany',
									JSON.stringify(this.financecompany),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}else{
														this.msg[0]='Error';
			 											this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.financecompanyForm.reset();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
														});
		 
	 }
}

export class FinanceCompanyData {
	
	fincompId: string ;
	fincompName: string;
	fincompActive: string ="true";
	fincompEntryId:string;
	fincompEntryDate:string;
    fincompModifiedId:string;
    fincompModifiedDate:string;
	
}


